import { recoilPersist } from 'recoil-persist';
const { persistAtom } = recoilPersist();
import { atom } from 'recoil';

interface StockModalState {
  visible: boolean;
  buttonState: string | undefined | null; // buy, sell, undefined
}

//  visible   -  & 
export const stockModalState = atom<StockModalState>({
  key: 'stockModalState',
  default: {
    visible: false,
    buttonState: undefined,
  },
});

interface StockModalVals {
  id: number | undefined; //  id
  kind: string; // (wallet)   (stock)  
  company_name: string | undefined; // 
  inStock: number | undefined; //  
  first_menu_price: number; //  /  
  second_menu_price: number; //   
  info: string | undefined; //  
  difference: number; //  
}

//       - ,   ,   ,  
export const stockModalVals = atom<StockModalVals>({
  key: 'stockModalVals',
  default: {
    id: undefined,
    kind: 'wallet',
    company_name: undefined,
    inStock: undefined,
    first_menu_price: 0,
    second_menu_price: 0,
    info: undefined,
    difference: 0,
  },
  effects_UNSTABLE: [persistAtom],
});
